import React, { useState } from 'react';
import { Button } from '~/ui/components';
import { Disclaimer } from '~/ui/components/Disclaimer/Disclaimer.component';
import { InputField } from '~/ui/components/InputField/InputField.component';
import { createRegisterTab, querySimpleLogin } from '~F/app/redux/app/background-link.saga';
import { useAppDispatch } from '~F/app/store';
import styles from './Login.module.scss';
import browser from '~/background/browser/browser';
import { sendMessage } from '~B/utils/messageEngine';

export default LoginView;

export function LoginView({ loggedIn, loginLoader }: { loggedIn: boolean; loginLoader: boolean }) {
    const [username, setUsername] = useState('');
    const [password, setPassword] = useState('');
    const [loginTries, setLoginTries] = useState(0);
    const doSimpleLogin = useAppDispatch(querySimpleLogin);

    const userFeedBack = () => {
        return (
            loginTries > 0 &&
            !loggedIn &&
            !loginLoader && (
                <div className={styles.feedBackWrapper}>
                    <span>Usuario o contraseña incorrectos</span>
                </div>
            )
        );
    };

    return (
        <div className={styles.container}>
            <div className={styles.formWrapper}>
                <h1 className={styles.header}>Inicia sesión en Chollometro</h1>
                <form
                    className={styles.loginForm}
                    onSubmit={(e) => {
                        e.preventDefault();
                        doSimpleLogin({ username, password });
                        setLoginTries(loginTries + 1);
                    }}
                >
                    <div className={styles.standardLogin}>
                        <InputField
                            id="email"
                            label="Nombre de usuario o email"
                            type="text"
                            onChange={setUsername}
                            value={username}
                            required
                            invalid={loginTries > 0 && !loggedIn && !loginLoader}
                        />
                        <InputField
                            id="password"
                            label="Contraseña"
                            type="password"
                            onChange={setPassword}
                            value={password}
                            required
                            invalid={loginTries > 0 && !loggedIn && !loginLoader}
                        />
                        {userFeedBack()}
                        <div className={styles.passRecall}>
                            <a
                                href="https://www.chollometro.com/password/reset"
                                target="_blank"
                                rel="noopener"
                            >
                                ¿Has olvidado tu contraseña?
                            </a>
                            <a
                                href="https://www.chollometro.com/agregar-contrasena"
                                target="_blank"
                                rel="noopener"
                            >
                                ¿Inicias sesión con Google, Facebook o Apple?
                            </a>
                        </div>
                    </div>
                    <div className={styles.buttonWrapper}>
                        {loginLoader ? (
                            <Button type="submit" text="Iniciar sesión" size="m" loading />
                        ) : (
                            <Button type="submit" text="Iniciar sesión" size={undefined} />
                        )}
                        <Button
                            type="submit"
                            buttonStyle="cold"
                            text="¡Regístrate en Chollometro!"
                            size={undefined}
                            onClick={() => {
                                const registerPageUrl = 'https://www.chollometro.com/register';

                                window.open(registerPageUrl, '_blank', 'noopener');
                            }}
                        />
                    </div>

                    {/* <div className={styles.passwordSupport}>
                        <div>
                            <span className={styles.userRecall}>*</span> Si tienes creada tu cuenta
                            de chollometro con una red social, debes crear tu contraseña.
                            <span>
                                {' '}
                                <a
                                    href="https://www.chollometro.com/agregar-contrasena"
                                    target="_blank"
                                    rel="noopener"
                                >
                                    Crea tu contraseña aquí
                                </a>
                            </span>
                        </div>
                    </div> */}
                </form>

                {/*   <div className={styles.socials}>
                    <div className={styles.socialsTitle}>
                        ------------ O continúa con ------------
                    </div>
                    <div className={styles.socialsIcons}>
                       
                        <Google />
                        <Facebook />
                        <Apple />
                    </div>
                    <div className={styles.socialsJoinNow}>
                        ¿Aún no perteneces a nuestra comunidad?{' '}
                        <span className={styles.socialsRegister}>Regístrate</span>
                    </div>
                </div> */}
            </div>
            <div className={styles.disclaimer}>
                {/* Disclaimer should be put on loginPlaceHolderview where does not need absolute posiotinig */}
                <Disclaimer text='Al interactuar con nuestra extensión, en ocasiones podemos abrir enlaces monetizados en segundo plano, así es cómo se financia Chollometro.<a href="https://www.chollometro.com/faqs#dinero" target="blank"> Más info</a>.' />
            </div>
        </div>
    );
}
