import React, { FunctionComponent, Suspense, useState } from 'react';
import { ErrorBoundary } from 'react-error-boundary';
import { ErrorDisplay, Loader } from '~/ui/components';

import { IPriceTrack, priceTrackAPI } from '../../api';
import { PriceTrackItem } from '../../components';

import styles from './PriceTrackConfig.module.scss';

import { WithSuspense } from '~/ui/utils/api/api.suspend';

const PriceTrackConfigView: FunctionComponent<{ data: IPriceTrack[] }> = ({ data }) => {
    const [priceTracks, setPriceTracks] = useState<IPriceTrack[]>(data);

    const deletePT = ({ id }: { id: string }) => {
        setPriceTracks(priceTracks.filter((elm) => elm.id !== id));
    };

    if (priceTracks.length === 0) {
        return <ErrorDisplay faceExpression="relieved" dialog="no_alert" />;
    }

    priceTracks.sort((elmA, elmB) => {
        const {
            product: { title: titleA },
        } = elmA;
        const {
            product: { title: titleB },
        } = elmB;
        return titleA.localeCompare(titleB);
    });

    return (
        <div className={styles.container}>
            {priceTracks.map((pT, index) => {
                return <PriceTrackItem priceTrack={pT} deletePT={deletePT} key={pT.id} />;
            })}
        </div>
    );
};

export default () => {
    return (
        <ErrorBoundary
            fallbackRender={() => {
                return <ErrorDisplay dialog="unknown" />;
            }}
        >
            <Suspense fallback={<Loader />}>
                <WithSuspense api={priceTrackAPI} component={PriceTrackConfigView} />
            </Suspense>
        </ErrorBoundary>
    );
};
