import { takeEvery } from 'redux-saga/effects';
import {
    answerSimpleLoginSaga,
    A_SIMPLE_LOGIN,
    LOGOUT,
    logOutSaga,
    querySimpleLoginSaga,
    Q_SIMPLE_LOGIN,
} from '~F/app/redux/app/background-link.saga';

export function* authSaga() {
    yield takeEvery(Q_SIMPLE_LOGIN, querySimpleLoginSaga);
    yield takeEvery(A_SIMPLE_LOGIN as any, answerSimpleLoginSaga);
    yield takeEvery(LOGOUT, logOutSaga);
}
