import { FaComment } from '@react-icons/all-files/fa/FaComment';
import React from 'react';
import { Currency, currencyToCurrencySign } from '~/ui/utils/currency';
import styles from './PriceShopSpec.module.scss';

export type IPriceShopSpecProps = {
    price: number;
    shop: string;
    comments?: number;
    lastPrice?: number | null;
} & ({ currency: Currency } | { currency?: undefined; currencySign: string });

export function PriceShopSpec(props: IPriceShopSpecProps) {
    const { comments, shop, lastPrice, price } = props;
    const currencySign = props.currency
        ? currencyToCurrencySign(props.currency)
        : props.currencySign;

    return (
        <div className={styles.container}>
            <span className={styles.price}>
                {price > 0 ? `${price.toFixed(2)}${currencySign}` : 'GRATIS'}
            </span>
            {typeof lastPrice !== 'number' ? null : (
                <>
                    <span className={styles.oldPrice}>
                        <span>
                            {lastPrice.toFixed(2)}
                            {currencySign}
                        </span>
                    </span>
                    <span className={styles.discount}>
                        (-{(100 * (1 - price / lastPrice)).toFixed(0)}%)
                    </span>
                </>
            )}
            <span className={styles.shop}>{shop}</span>
            {comments === undefined ? null : (
                <span className={styles.comments}>
                    <FaComment />
                    {comments}
                </span>
            )}
        </div>
    );
}
