import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { addExtensionUTMParamsToLink } from '~/ui/utils/UTMLinks';
import { IOfferState } from './offer.types';

const initialState: IOfferState = {
    offers: [],
    shop: '',
};

const offerSlice = createSlice({
    name: 'offer',
    initialState,
    reducers: {
        setOffers(state, { payload }: PayloadAction<IOfferState>) {
            payload.offers?.forEach((o) => {
                o.url = addExtensionUTMParamsToLink(o.url, 'extension_ofertas');
            });
            return payload;
        },
    },
});

export default offerSlice.reducer;
export const { setOffers } = offerSlice.actions;
