export type Currency = 'EUR';
export type CurrencySign = '€';

export function currencyToCurrencySign(currency: Currency): CurrencySign {
    switch (currency) {
        case 'EUR':
            return '€';
    }
}

export function currencySignToCurrency(currencySign: CurrencySign): Currency {
    switch (currencySign) {
        case '€':
            return 'EUR';
    }
}
