import React from 'react';
import styles from './OptionSelect.module.scss';

type Tag = string | number;
export type SelectOption<TValue extends Tag = Tag, TData extends any = any> = {
    label: string;
    value: TValue;
    data?: TData;
};

export interface IOptionSelectBaseProps<TValue extends Tag = Tag, TData = any> {
    onChange: (value: TValue, selectedIndex: number, option: SelectOption<TValue, TData>) => void;
    value: TValue;
    label?: string;
    layout?: 'COL' | 'ROW';
}

export interface IOptionSelectProps<TValue extends Tag = Tag, TData = any>
    extends IOptionSelectBaseProps<TValue, TData> {
    options: SelectOption<TValue, TData>[];
}

export const OptionSelect = <TValue extends Tag = Tag, TData = any>({
    options,
    onChange,
    value,
    label,
    layout = 'ROW',
}: IOptionSelectProps<TValue, TData>) => {
    return (
        <div className={styles.base + ' ' + (layout === 'ROW' ? styles.row : styles.col)}>
            {label && <label>{label}</label>}
            <select
                value={value}
                onChange={(e) =>
                    onChange(
                        e.target.value as TValue,
                        e.target.selectedIndex,
                        options[e.target.selectedIndex],
                    )
                }
            >
                {options.map((elm, idx) => {
                    return (
                        <option key={elm.value} value={elm.value}>
                            {elm.label}
                        </option>
                    );
                })}
            </select>
        </div>
    );
};
