import { IInputFieldProps } from './InputField.component';
import { IInputInnerProps } from './InputInner/InputInner';

export function createPriceInputProps(props: IInputFieldProps): IInputInnerProps {
    const { onChange, onKeyUpEnter } = props;
    const onlyNumbersR = /^\d+((\.|\,)(\d{1,2})?)?$/g;
    return {
        ...props,

        onChange(value, id) {
            if (onlyNumbersR.test(value)) {
                if (value.length > 2) {
                    if (value[0] === '0' && value[1] !== '.' && value[1] !== ',') {
                        onChange?.(value.slice(1, value.length), id);
                        return;
                    }
                }

                onChange?.(value, id);
            }
        },
        onKeyDown(e) {
            const { value, id } = props;
            if (e.key === 'Backspace' && value?.length === 1) {
                onChange?.('', id);
            }
            if (e.key === 'Backspace' && (value?.endsWith('.') || value?.endsWith(','))) {
                const decimalSeparationDel = value?.slice(0, value?.length - 2);

                onChange?.(decimalSeparationDel, id);
                e.preventDefault();
            }
            if (e.key === 'Enter' && onlyNumbersR.test(value as string)) {
                onKeyUpEnter(e);
            }
        },
    };
}
