import { ReactNode } from 'hoist-non-react-statics/node_modules/@types/react';
import React, { BaseSyntheticEvent, EventHandler, KeyboardEventHandler } from 'react';
import { createPriceInputProps } from './InputField.util';
import { InputInner } from './InputInner';
import { OnChangeEvent } from './InputInner/InputInner';

export interface IInputFieldProps {
    id: string;
    locked?: boolean;
    focused?: boolean;
    value?: string;
    error?: string;
    label?: string;
    placeholder?: string;
    footerNode?: ReactNode;
    onChange?: OnChangeEvent;
    onKeyUp?: KeyboardEventHandler;
    onKeyUpEnter: KeyboardEventHandler;
    required?: boolean;
    type?: 'password' | 'number' | 'text' | 'email' | 'price';
    step?: number;
    data?: string;
    invalid?: boolean;
}

export function InputField(props: IInputFieldProps) {
    const { type } = props;

    if (type === 'price') return <InputInner {...createPriceInputProps(props)} />;

    return <InputInner {...props} />;
}
