import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { addExtensionUTMParamsToLink } from '~/ui/utils/UTMLinks';
import { IOffer } from '../offer/offer.types';
import { ISimilarState } from './similar.types';

const initialState: ISimilarState = {
    offers: [],
};

const similarSlice = createSlice({
    name: 'similar',
    initialState,
    reducers: {
        setSimilar(state, action: PayloadAction<IOffer[]>) {
            action.payload?.forEach((o) => {
                o.url = addExtensionUTMParamsToLink(o.url, 'extension_similares');
            });
            state.offers = action.payload;
        },
    },
});

export default similarSlice.reducer;
export const { setSimilar } = similarSlice.actions;
