import { all, call, put } from 'redux-saga/effects';
import { isBrowserActionPage } from '~/ui/utils/BrowserDetection';
import logger from '~/ui/utils/logger';
import { IUserSettings, settingsAPIHandler } from '~F/account/api';
import { authSaga } from '~F/account/redux/account/auth.saga';

import { setMenuOpen } from './app.reducer';
import { querySimpleLogin, watchBackgroundSaga } from './background-link.saga';

export function* bootSaga() {
    const settings: IUserSettings = yield call(settingsAPIHandler.read);
    yield put(setMenuOpen(!settings.initWithCompactMenu));
    // login status check to avoid async logic on popup view

    const localUserKey = 'loginStatus';
    if (isBrowserActionPage()) {
        logger.log('live in action popup page');

        return;
    }
    yield put(querySimpleLogin());
}

export default function* appSaga() {
    const systemSagas = [bootSaga(), authSaga(), watchBackgroundSaga()];
    if (
        import.meta.env.VITE_NODE_ENV !== 'production' ||
        import.meta.env.VITE_INCLUDE_UTILS === 1
    ) {
        const debugSaga = import.meta.glob('../../../debug/redux/debug/debug.saga.ts', {
            eager: true,
        });

        systemSagas.push(debugSaga['../../../debug/redux/debug/debug.saga.ts'].default());
    }
    yield all(systemSagas);
}
