import { FaHeartbeat } from '@react-icons/all-files/fa/FaHeartbeat';
import { FaRegClock } from '@react-icons/all-files/fa/FaRegClock';
import moment from 'moment';
import React from 'react';
import styles from './IconTime.module.scss';

export type TimingIcon = 'heartbeat' | 'clock';
export interface IIconTimeProps {
    time: number;
    label?: string;
    icon?: TimingIcon;
}

const Icon = ({ icon }: { icon: TimingIcon }) => {
    switch (icon) {
        case 'clock':
            return <FaRegClock />;
        case 'heartbeat':
            return <FaHeartbeat />;
    }
};

const anHour = 1000 * 60 * 60;
export const IconTime = ({ time, label, icon = 'clock' }: IIconTimeProps) => {
    moment.locale('es');
    const currentTime = moment();
    const inputTime = moment.unix(time);
    const delta = currentTime.diff(inputTime);

    const labelledFormat = label ? `${label} ` : '';

    let text = `${labelledFormat}${inputTime.format(`DD MMM`).substring(0, 6)}`;

    if (delta < anHour) {
        text = moment.utc(delta).format(`[${labelledFormat}][hace] [0h], m[m]`);
    } else if (delta < anHour * 24) {
        text = moment.utc(delta).format(`[${labelledFormat}][hace] h[h], m[m]`);
    }

    return (
        <div className={styles.container}>
            <Icon icon={icon} />
            <span>{text}</span>
        </div>
    );
};
