import classNames from 'classnames';
import React, { useState } from 'react';
import { IconTime, InlineOkCancel } from '~/ui/components';
import { InputField } from '~/ui/components/InputField/InputField.component';
import { TimeSelect } from '../TimeSelect/TimeSelect.component';
import styles from './PriceTrackInput.module.scss';

export interface IPriceTrackInputProps {
    mode?: 'CREATE' | 'UPDATE';
    confirmLabel: string;
    onConfirm: (price: number, exPDays: number) => void;
    cancelLabel: string;
    onCancel: () => void;
    initialValue?: number;
    loading?: boolean;
    trackingPrice: number;
    expirationDays?: number;
    trackingStartMs?: number;
    isLoading?: boolean;
    focus?: boolean;
}

export const PriceTrackInput = (props: IPriceTrackInputProps) => {
    const {
        mode = 'CREATE',
        expirationDays,
        onConfirm,
        onCancel,
        focus,
        confirmLabel,
        cancelLabel,
        loading,
        trackingPrice,
        trackingStartMs,
        isLoading,
    } = props;

    const initialPrice = mode === 'CREATE' ? trackingPrice * 0.95 : trackingPrice;

    const [timeSelectValue, setTimeSelectValue] = useState(expirationDays || 360);
    const [price, setPrice] = useState(initialPrice.toFixed(2));

    const priceToNumberUtil = (price: string) => {
        return parseFloat(price.replace(',', '.'));
    };

    return (
        <div className={styles.container}>
            <div className={styles.wrappSelector}>
                {mode === 'UPDATE' && !!trackingStartMs && (
                    <IconTime
                        time={Math.round(trackingStartMs / 1000)}
                        icon="clock"
                        label="En seguimiento desde"
                    />
                )}
                <div
                    className={classNames(styles.timeSelect, {
                        [styles.timeSelectStart]: !trackingStartMs,
                    })}
                >
                    <TimeSelect
                        label="Seguir durante:"
                        onChange={(v) => setTimeSelectValue(+v)}
                        value={timeSelectValue}
                    />
                </div>
                {/* {mode === 'CREATE' && (
                    <PriceSelect
                        label="Descuento:"
                        onChange={setPrice}
                        value={price}
                        basePrice={trackingPrice}
                    />
                )} */}
            </div>

            <div className={styles.inputControls}>
                <InlineOkCancel
                    cancelLabel={cancelLabel}
                    onCancel={onCancel}
                    okLabel={confirmLabel}
                    onOk={() => {
                        onConfirm(priceToNumberUtil(price), timeSelectValue);
                    }}
                    okButtonStyle="normal"
                    cancelButtonStyle="normal"
                    okLoading={isLoading}
                >
                    {
                        <div className={styles.inputWrapper}>
                            <InputField
                                id="newPrice"
                                label="Precio inferior a"
                                onChange={setPrice}
                                value={'' + price}
                                onKeyUpEnter={(e) => {
                                    onConfirm(priceToNumberUtil(price), timeSelectValue);
                                }}
                                type="price"
                                focused={focus}
                            />
                        </div>
                    }
                </InlineOkCancel>
            </div>
        </div>
    );
};
