import React, { ComponentType, ReactNode, useState } from 'react';
import { ReactComponent as Close } from '~A/icons/cross.inline.svg';
import styles from './InfoWrapper.module.scss';
import classNames from 'classnames';

export interface IInfoWrapperProps {
    title: ReactNode;
    onClose?: () => void;
    children?: ReactNode;
    iconSVG?: ComponentType;
    footer?: ReactNode;
    isLogo?: boolean;
}

function NoContent() {
    return <div className={styles.noContent}>No hay datos que mostrar</div>;
}

export function InfoWrapper({
    footer = null,
    title,
    iconSVG: Icon,
    onClose,
    children = null,
    isLogo = false,
}: IInfoWrapperProps) {
    const [isScrolling, setIsScrolling] = useState(false);
    return (
        <div className={styles.container}>
            <div
                className={styles.content}
                onScroll={(event) => {
                    if ((event.target as HTMLDivElement).scrollTop > 0) {
                        setIsScrolling(true);
                    } else {
                        setIsScrolling(false);
                    }
                }}
            >
                <div
                    className={classNames(styles.header, {
                        [styles.headerShadow]: isScrolling,
                    })}
                    id={
                        import.meta.env.VITE_NODE_ENV === 'test' ? `assistant-app-title` : undefined
                    }
                >
                    <div className={styles.icon}>{Icon ? <Icon /> : null}</div>
                    <span className={styles.title}>{title}</span>{' '}
                    {onClose && (
                        <div
                            className={styles.closeButton}
                            id={
                                import.meta.env.VITE_NODE_ENV === 'test'
                                    ? `assistant-app-inclose`
                                    : undefined
                            }
                            onClick={onClose}
                        >
                            <Close />
                        </div>
                    )}
                </div>

                {children || <NoContent />}
            </div>
            {footer && <div className={styles.footer}>{footer}</div>}
        </div>
    );
}

export default InfoWrapper;
