import React, { ReactElement, useEffect, useState } from 'react';
import { useDispatch } from 'react-redux';
import { InfoWrapper, Loader } from '~/ui/components';
import styles from '~/ui/components/InfoWrapper/InfoWrapper.module.scss';
import { isBrowserActionPage } from '~/ui/utils/BrowserDetection';
import { ReactComponent as Logo } from '~A/icons/logo.svg';
import { UserData } from '~B/storage';
import { setLoginStatus } from '~F/account/redux/account';
import { closeView } from '~F/app/redux/app';
import { useAppDispatch, useAppSelector } from '~F/app/store';
import LoginView from '../Login/Login.view';
import profileStyles from '../Profile/Profile.module.scss';

export function LoginPlaceHolderView({
    PlaceHolderView,
    autoClose,
}: {
    PlaceHolderView: () => ReactElement;
    autoClose: boolean;
}) {
    const { loginStatus, loginLoader } = useAppSelector((state) => ({
        loginStatus: state.alertas.loginStatus,
        loginLoader: state.alertas.logInLoader,
    }));
    const [popUpLoading, setPopupLoading] = useState(true);
    const onClose = useAppDispatch(closeView);

    const { loggedIn } = loginStatus;
    const dispatch = useDispatch();

    useEffect(() => {
        UserData.get().then((userData) => {
            if (userData?.userId) {
                dispatch(setLoginStatus({ ...userData, loggedIn: true }));
            } else {
                dispatch(setLoginStatus({ ...userData, loggedIn: false }));
            }
            setPopupLoading(false);
        });
    }, []);
    if (isBrowserActionPage() && popUpLoading) {
        return (
            <div className={profileStyles.container}>
                <Loader />
            </div>
        );
    }
    return loggedIn ? (
        <PlaceHolderView />
    ) : (
        <InfoWrapper
            title={'Chollometro'}
            iconSVG={() => <Logo className={styles.brandmark} />}
            onClose={autoClose && onClose}
        >
            <LoginView loggedIn={loggedIn} loginLoader={loginLoader} />
        </InfoWrapper>
    );
}
