import React from 'react';
import { IErrorDisplayPresenterProps } from '../ErrorDisplay.types';
import styles from './ErrorPresenter.module.scss';

export const MainErrorPresenter = ({
    icon,
    message,
    userAction,
    facialComment,
}: IErrorDisplayPresenterProps) => {
    return (
        <div className={styles.errorDisplayWrapper}>
            <div className={styles.iconWrapper}>{icon}</div>
            <TextDisplay iconText={message} userText={userAction} facialComment={facialComment} />
        </div>
    );
};

// {faceExpression === 'ashame' && (
