//     <AshamedFace width="90px" height="107px" />
// )}
// {faceExpression === 'relieved' && <RelievedFace width="105px" height="105px" />}

const TextDisplay = ({
    iconText,
    userText,
    facialComment,
}: {
    iconText: string;
    userText: string;
    facialComment: string;
}) => {
    return (
        <div className={styles.dialogContent}>
            <p className={styles.facialComment}>{facialComment}</p>
            <p className={styles.iconText}>{iconText}</p>

            {typeof userText === 'string' ? <p>{userText}</p> : userText}
        </div>
    );
};
