import classNames from 'classnames';
import React, { KeyboardEventHandler, ReactNode, useEffect, useRef, useState } from 'react';
import styles from './InputInner.module.scss';

export type OnChangeEvent = (value: string, id: string) => void;

export interface IInputInnerProps {
    id: string;
    locked?: boolean;
    focused?: boolean;
    value?: string;
    error?: string;
    label?: string;
    placeholder?: string;
    footerNode?: ReactNode;
    onChange?: OnChangeEvent;
    onKeyDown?: KeyboardEventHandler;
    onKeyUp?: KeyboardEventHandler;
    onKeyUpEnter: KeyboardEventHandler;
    required?: boolean;
    type?: 'password' | 'number' | 'text' | 'email' | 'price';
    step?: number;
    data?: string;
    invalid?: boolean;
}

export function InputInner(props: IInputInnerProps) {
    const [focused, setFocused] = useState((props.locked && props.focused) || false);

    const textInput = useRef<HTMLInputElement>(null);
    useEffect(() => {
        if (props.focused) {
            textInput.current?.focus();
        }
    }, []);
    const {
        id,
        onChange = () => {
            /* Intentionally empty */
        },
        onKeyUpEnter,
        value,
        placeholder = '',
        label,
        footerNode,
        type = 'text',
        required,
        step,
        onKeyDown,
        data,
        invalid,
    } = props;

    const showLabel = !!label;

    return (
        <div
            className={classNames({
                [styles.container]: true,
                [styles.containerFocus]: focused,
                [styles.invalid]: invalid,
            })}
            onClick={() => !focused && textInput?.current?.focus()}
        >
            {showLabel && (
                <label
                    htmlFor={id}
                    className={classNames({
                        [styles.priceTheme]: type === 'price',
                    })}
                >
                    {label}
                    {/*  {required && <span className={styles.required}>*</span>} */}
                </label>
            )}
            <input
                onKeyDown={onKeyDown} // treat callback management at parent state
                /* (e) => {
                    if (type === 'number' && e.key === '.') {
                        e.preventDefault();
                    }
                    if (type === 'text' && e.key === 'Enter') {
                        onKeyDown!(e);
                    }
                } */
                data-input={data}
                step={step}
                className={classNames(styles.inputElement, {
                    [styles.priceTheme]: type === 'price',
                })}
                required={required}
                ref={textInput}
                id={id}
                type={type}
                value={value}
                onFocus={() => setFocused(true)}
                onBlur={() => setFocused(false)}
                onChange={(e) => e?.target && onChange(e.target.value, id)}
                placeholder={placeholder && placeholder}
            />
            {type === 'password' && <span></span>}
            {footerNode ? footerNode : null}
        </div>
    );
}
