import classNames from 'classnames';
import React, { ReactNode } from 'react';
import { Button } from '~/ui/components';
import logger from '~/ui/utils/logger';
import styles from './InlineOkCancel.module.scss';

export interface IInlineOkCancelProps {
    onOk: () => void;
    onCancel: () => void;
    children: ReactNode;
    cancelButtonStyle?: 'normal' | 'hot' | 'cold';
    okLabel: string;
    okButtonStyle?: 'normal' | 'hot' | 'cold';
    cancelLabel: string;
    okLoading?: boolean;
}

export const InlineOkCancel = ({
    onOk,
    onCancel,
    children,
    okLabel,
    okButtonStyle,
    okLoading,
    cancelLabel,
    cancelButtonStyle,
}: IInlineOkCancelProps) => {
    logger.log(okLoading, 'isloading');

    return (
        <div
            className={classNames(styles.container, {
                [styles.containerCancel]: okLabel === 'Eliminar',
            })}
        >
            <div
                className={classNames(styles.slot, {
                    [styles.slotCancel]: okLabel === 'Eliminar',
                })}
            >
                {children}
            </div>

            <Button
                text={cancelLabel}
                size="m"
                radius
                buttonStyle={cancelButtonStyle}
                variant="secondary"
                onClick={onCancel}
            />
            <Button
                text={okLabel}
                size="m"
                radius
                buttonStyle={okButtonStyle}
                variant="primary"
                onClick={onOk}
                loading={okLoading}
            />
        </div>
    );
};
