import classNames from 'classnames';
import React, { useCallback, useEffect, useRef, useState } from 'react';
import { ReactComponent as Trash } from '~/ui/assets/icons/trash.inline.svg';
import { ReactComponent as Pen } from '~/ui/assets/icons/pen.inline.svg';

import { ImageBox, PriceShopSpec } from '~/ui/components';
import { IPriceTrack, priceTrackAPI } from '../../api';
import { PriceTrackInput } from '../PriceTrackInput';

import styles from './PriceTrackItem.module.scss';

import anime from 'animejs/lib/anime.es.js';
import logger from '~B/utils/logger';
import { metricAlertsApi } from '~F/app/api/metrics/metricsAlerts.api';

type PriceTrackItemMode = 'VIEW' | 'DELETE' | 'EDIT';

export interface IPriceTrackItemProps {
    priceTrack: IPriceTrack;
    deletePT: ({ id }: { id: string }) => void;
}
interface IAnimPropsPt {
    alertContainer: HTMLDivElement;
    scrollTopHeight: number;
    alertPickerNode: HTMLElement;
    editHeight: number;
    alertItemNode: HTMLElement;
    alertItemHeight: number;
    delButtonNode: HTMLDivElement;
    inputNode: HTMLInputElement;
}
export const PriceTrackItem = ({ priceTrack, deletePT }: IPriceTrackItemProps) => {
    const [mode, setModeRaw] = useState<PriceTrackItemMode>('VIEW');
    const [loading, setLoading] = useState(false);
    const [updatePrice, setUpdatePrice] = useState(priceTrack.expectedPrice);
    const alertPickerRef = useRef<HTMLDivElement>();
    const delButtonRef = useRef<HTMLDivElement>();
    const alertItemRef = useRef<HTMLDivElement>();
    const animPropsRef = useRef<IAnimPropsPt>();

    useEffect(() => {
        animPropsRef.current = {
            alertPickerNode: alertPickerRef.current,
            get editHeight() {
                return this.alertPickerNode.clientHeight;
            },
            alertItemNode: alertItemRef.current,
            get inputNode() {
                return this.alertPickerNode.querySelector('#newPrice') as HTMLInputElement;
            },
            get alertItemHeight() {
                return this.alertItemNode.clientHeight;
            },
            delButtonNode: delButtonRef.current,
            get alertContainer() {
                return this.alertItemNode.parentElement?.parentElement as HTMLDivElement;
            },
            get scrollTopHeight() {
                return this.alertPickerNode.getBoundingClientRect().y;
            },
        } as IAnimPropsPt;
        const { alertPickerNode, editHeight } = animPropsRef.current;
        if (alertPickerNode) {
            alertPickerNode.style.marginTop = `${-editHeight}px`;
        }
    }, [priceTrack, mode]);
    const delTimeLIne = anime.timeline({
        easing: 'easeOutExpo',
        duration: 750,
    });

    const setMode = useCallback(
        (newMode: PriceTrackItemMode) => {
            setModeRaw(newMode === mode ? 'VIEW' : newMode);
        },
        [mode],
    );
    const onConfirm = async (price: number, expirationDays: number) => {
        try {
            setLoading(true);
            await priceTrackAPI.methods.update({
                ...priceTrack,
                expectedPrice: price,
                expirationDays: expirationDays.toString(),
            });
            metricAlertsApi.methods.metricAlerts({
                name: 'ext-event-price-track-edit',
                data: {
                    productTitle: priceTrack.product.title,
                    productImageUrl: priceTrack.product.imageUrl,
                    productUrl: priceTrack.product.url,
                    merchantDomain: new URL(priceTrack.product.url).host.replace(/^w+\./, ''),
                },
            });
            setLoading(false);
            setUpdatePrice(price);
            setMode('VIEW');
        } catch (error) {}
    };
    const onDelete = async () => {
        try {
            deletePT({ id: priceTrack.id });
            await priceTrackAPI.methods.remove(priceTrack);
            metricAlertsApi.methods.metricAlerts({
                name: 'ext-event-price-track-delete',
                data: {
                    productTitle: priceTrack.product.title,
                    productImageUrl: priceTrack.product.imageUrl,
                    productUrl: priceTrack.product.url,
                    merchantDomain: new URL(priceTrack.product.url).host.replace(/^w+\./, ''),
                },
            });
            logger.log('alerta borrada correctamente');
        } catch (error) {
            logger.error('Something went wrong removing the priceTrack', error as object);
            throw error;
        }
    };

    return (
        <div ref={alertItemRef} className={styles.container} key={priceTrack.id}>
            <div className={styles.item}>
                <a
                    href={`https://chollometrocextension.digidip.net/visit?url=${encodeURI(
                        priceTrack.product.url as string,
                    )}`}
                    target="_blank"
                >
                    <ImageBox imageUrl={priceTrack.product.imageUrl} />
                </a>

                <div className={styles.description}>
                    <div
                        className={styles.title}
                        onClick={() => {
                            const urlTracking = `https://chollometrocextension.digidip.net/visit?url=${encodeURI(
                                priceTrack.product.url as string,
                            )}`;
                            window.open(urlTracking, '_blank', 'noopener');
                        }}
                        title={priceTrack.product.title}
                    >
                        {priceTrack.product.title}
                    </div>

                    <div className={styles.editBox}>
                        <PriceShopSpec
                            price={updatePrice}
                            shop={priceTrack.shop}
                            currencySign={'€'}
                        />
                        {mode == 'VIEW' && (
                            <a
                                onClick={() => {
                                    setMode('EDIT');

                                    const { alertPickerNode, inputNode, alertItemNode } =
                                        animPropsRef.current as IAnimPropsPt;

                                    anime({
                                        targets: alertPickerNode,
                                        marginTop: 0,
                                        complete(anim) {
                                            alertItemNode.scrollIntoView({
                                                behavior: 'smooth',
                                                block: 'end',
                                            });
                                            setTimeout(() => {
                                                inputNode.focus();
                                            }, 500);
                                        },
                                    });
                                }}
                                className={classNames(styles.edit)}
                            >
                                <Pen />
                            </a>
                        )}
                        {mode === 'VIEW' && (
                            <div
                                ref={delButtonRef}
                                className={styles.trashContainer}
                                onClick={() => {
                                    const { delButtonNode, alertItemNode, alertItemHeight } =
                                        animPropsRef.current as IAnimPropsPt;
                                    delTimeLIne
                                        .add({
                                            targets: delButtonNode,
                                            width: '100%',
                                        })
                                        .add({
                                            targets: alertItemNode,
                                            keyframes: [
                                                { opacity: 0 },
                                                { marginTop: `-${alertItemHeight}px` },
                                            ],
                                            complete(anim) {
                                                onDelete();
                                            },
                                        });

                                    //onDelete()
                                }}
                            >
                                <Trash />
                            </div>
                        )}
                    </div>
                </div>
            </div>
            <div className={styles.controls}>
                <div id="edit" ref={alertPickerRef} className={styles.editWrapper}>
                    <PriceTrackInput
                        onConfirm={onConfirm}
                        onCancel={() => {
                            const { alertPickerNode, editHeight } =
                                animPropsRef.current as IAnimPropsPt;
                            anime({
                                targets: alertPickerNode,
                                marginTop: `${-editHeight}px`,
                            });
                            setMode('VIEW');
                        }}
                        confirmLabel="Guardar"
                        cancelLabel="Cancelar"
                        mode={'UPDATE'}
                        trackingPrice={priceTrack.expectedPrice}
                        expirationDays={priceTrack.expirationDays}
                        trackingStartMs={priceTrack.createdAt}
                        isLoading={loading}
                    />
                </div>
            </div>
        </div>
    );
};
