import classNames from 'classnames';
import moment from 'moment';
import React, { MouseEvent, useEffect, useRef, useState } from 'react';
import { ReactComponent as Trash } from '~/ui/assets/icons/trash.inline.svg';
import { ImageBox, PriceShopSpec } from '~/ui/components';

import { IPriceTrackNotification } from '../../api';

import styles from './PriceTrackNotificationItem.module.scss';
import { timeAgo } from './PriceTrackNotificationItem.utils';
import anime from 'animejs';

export interface IPriceTrackNotificationItemProps {
    notification: IPriceTrackNotification;

    deleteNoti: (id: string) => void;
}
interface IAnimProps {
    notiItemNode: HTMLElement;
    notiItemHeight: number;
    delButtonNode: HTMLElement;
}
export function PriceTrackNotificationItem({
    notification,
    deleteNoti,
}: IPriceTrackNotificationItemProps) {
    const [showDialog, setIshowDialog] = useState(false);

    const delButtonRef = useRef<HTMLElement>();
    const notificationItemRef = useRef<HTMLElement>();
    const animPropsRef = useRef<IAnimProps>();
    useEffect(() => {
        animPropsRef.current = {
            notiItemNode: notificationItemRef.current,
            get notiItemHeight() {
                return this.notiItemNode.clientHeight;
            },

            delButtonNode: delButtonRef.current,
        } as IAnimProps;
    }, [notification]);
    const delTimeLIne = anime.timeline({
        easing: 'easeOutExpo',
        duration: 750,
    });
    moment.locale('es');

    const fromPrice = Math.max(
        notification.creationPrice,
        notification.expectedPrice,
        notification.product.lastPrice || 0,
    );
    const toPrice = notification.product.price;

    return (
        <div ref={notificationItemRef} className={styles.container}>
            <div
                className={styles.content}
                onClick={() => {
                    const urlTracking = `https://chollometrocextension.digidip.net/visit?url=${encodeURI(
                        notification.product.url as string,
                    )}`;
                    window.open(urlTracking, '_blank', 'noopener');
                }}
            >
                <ImageBox imageUrl={notification.product.imageUrl} />
                <div className={styles.notification}>
                    <div className={styles.timeAgoTrash}>
                        <div className={styles.timeAgo}>
                            {timeAgo(notification.notifiedAt ?? notification.createdAt)}
                        </div>
                    </div>
                    <div className={styles.title} title={notification.product.title}>
                        {notification.product.title}
                    </div>
                    <div className={styles.priceWrapper}>
                        <PriceShopSpec
                            price={toPrice}
                            shop={notification.product.shop}
                            lastPrice={fromPrice}
                            currencySign={'€'}
                        />
                        <div
                            ref={delButtonRef}
                            className={classNames('item', styles.trash, {
                                [styles.trashSelected]: showDialog,
                            })}
                            onClick={(e: MouseEvent) => {
                                e.stopPropagation();
                                const { delButtonNode, notiItemNode, notiItemHeight } =
                                    animPropsRef.current as IAnimProps;
                                delTimeLIne
                                    .add({
                                        targets: delButtonNode,
                                        width: '100%',
                                    })
                                    .add({
                                        targets: notiItemNode,
                                        keyframes: [
                                            { opacity: 0 },
                                            { marginTop: `-${notiItemHeight}px` },
                                        ],
                                        complete(anim) {
                                            deleteNoti(notification.priceTrackNotificationId);
                                        },
                                    });
                            }}
                        >
                            <Trash />
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}
