import React, { ComponentProps, FunctionComponent, ReactPropTypes } from 'react';
import logger from '../logger';
import { APIHandler } from '.';
import { Component } from '@storybook/blocks';

interface INet {
    status: 'pending' | 'fullfilled' | 'error';
    result: any;
}
const FetchStatus = <T,>({ Component, net }: { Component: FunctionComponent<{}>; net: INet }) => {
    logger.log('api', net);
    if (net.status === 'pending') {
        throw net.result;
    }
    if (net.status === 'error') {
        throw net.result;
    }

    return <Component />;
};

export function WithSuspense<Iapi, Idata>({
    api,
    component,
    ...props
}: {
    api: APIHandler<Iapi, Idata>;
    component: FunctionComponent<{
        data: Idata;
        api: APIHandler<Iapi, Idata>;
        props: ComponentProps<any>;
    }>;
    props: ComponentProps<any>;
}) {
    api.connect();

    const net: INet = {
        status: 'pending',
        result: null,
    };

    const apiPromise = api.read();
    net.result = apiPromise;

    apiPromise
        .then((data) => {
            net.status = 'fullfilled';
            net.result = data;
        })
        .catch((error) => {
            net.status = 'error';
            net.result = error;
        });
    logger.log('loading api', apiPromise, props);
    return (
        <FetchStatus net={net} Component={() => component({ data: net.result, api, ...props })} />
    );
}
