import { Dispatch, useCallback } from 'react';
import { TypedUseSelectorHook, useDispatch, useSelector } from 'react-redux';
import {
    Action,
    AnyAction,
    applyMiddleware,
    combineReducers,
    compose,
    createStore,
    Store,
} from 'redux';
import logger from 'redux-logger';
import createSagaMiddleware from 'redux-saga';
import browser from '~B/browser/browser';
import account, { IAccountState } from '~F/account/redux/account';
// import  from '~F/app/redux/app/app.reducer';
import app, { appSaga, IAppState, IView } from '~F/app/redux/app';
import { IDebugState } from '~F/debug/redux/debug';
import debugReducer from '~F/debug/redux/debug/debug.reducer';
import offer, { IOfferState } from '~F/pepper-posts/redux/offer';
import postedOfferReducer, {
    IPostedOfferState,
} from '~F/pepper-posts/redux/posted-offer/postedOffer.reducer';
import similar, { ISimilarState } from '~F/pepper-posts/redux/similar';
import voucher, { IVoucherState } from '~F/pepper-posts/redux/voucher';
import productStats, { IPriceState } from '~F/product-stats/redux';

export interface IReducer<T> {
    (state: T, action: { type: string; payload: any }): T;
}

export interface IViewState
    extends IView<
        IPriceState,
        ISimilarState,
        IVoucherState,
        IOfferState,
        IAccountState,
        IPostedOfferState
    > {}

export interface IAppReducers
    extends IView<
        IReducer<IPriceState>,
        IReducer<ISimilarState>,
        IReducer<IVoucherState>,
        IReducer<IOfferState>,
        IReducer<IAccountState>,
        IReducer<any>
    > {
    app: IReducer<IAppState>;
    debug?: IReducer<IDebugState>;
}

export interface AppState extends IViewState {
    app: IAppState;
    debug: IDebugState;
}

const sagaMiddleware = createSagaMiddleware();

// Begin - Standard config
const reducerObject: IAppReducers = {
    app,
    voucher,
    offer,
    similar,
    productStats: productStats,
    alertas: account,
    postedOffer: postedOfferReducer,
};

const middlewares: any[] = [sagaMiddleware];
const preEnhancers: any[] = [];
const postEnhancers: any[] = [];

// End - Standard config

// Begin - Custom environment config

// Only include debug code on development builds
if (import.meta.env.VITE_NODE_ENV === 'development') {
    middlewares.push(logger);
}
if (import.meta.env.VITE_NODE_ENV !== 'production' || import.meta.env.VITE_INCLUDE_UTILS) {
    // reducerObject['debug'] = (await import('~F/debug/redux/debug/debug.reducer')).default;
    reducerObject.debug = debugReducer;
}

// End - Custom environment config

// Load config into system
const appReducer = combineReducers(reducerObject as any);

const rootReducer = (state: any, action: any) => {
    if (action.type === 'RESET_STORE') {
        return appReducer(undefined, action as never);
    }

    return appReducer(state, action as never);
};

window.onload = function () {
    let oldHref = document.location.href;
    const bodyList = document.querySelector('body');

    const observer = new MutationObserver(function (mutations) {
        mutations.forEach(function (mutation) {
            if (oldHref != document.location.href) {
                oldHref = document.location.href;
                store.dispatch({ type: 'RESET_STORE' });
            }
        });
    });

    const config = {
        childList: true,
        subtree: true,
    };

    observer.observe(bodyList as Node, config);
};

export const store: Store<AppState> = createStore(
    rootReducer,
    compose(...preEnhancers, applyMiddleware(...middlewares), ...postEnhancers),
);

sagaMiddleware.run(appSaga);

// background-link
browser.runtime.onMessage.addListener(function backToFront(m: AnyAction) {
    store.dispatch(m);
});

export function useAppDispatch(): Dispatch<any>;
export function useAppDispatch(fn: () => Action): () => void;
export function useAppDispatch<TPayload>(
    fn: (payload: TPayload) => Action,
): (payload: TPayload) => void;

// Use throughout your app instead of plain `useDispatch` and `useSelector`
export function useAppDispatch<TPayload>(
    actionCreator?: (payload: TPayload) => Action<TPayload>,
): any {
    const dispatch = useDispatch();
    if (actionCreator === undefined) return dispatch;
    return useCallback((payload: TPayload) => void dispatch(actionCreator(payload)), [dispatch]);
}
export const useAppSelector: TypedUseSelectorHook<AppState> = useSelector;
