import React from 'react';
import {
    IOptionSelectBaseProps,
    OptionSelect,
} from '~/ui/components/OptionSelect/OptionSelect.component';

interface TimeSelectProps extends IOptionSelectBaseProps {}
export const rightTimeRange = (days: number, options: { label: string; value: number }[]) => {
    for (const opt of options.reverse()) {
        if (days >= opt.value) {
            return opt.value;
        }
    }
    return options[options.length - 1].value;
};

export const TimeSelect = ({ onChange, value, label }: TimeSelectProps) => {
    let validValue: number;
    const monthDays = 30;
    const options = [
        { label: '1 mes', value: monthDays },
        { label: '3 meses', value: monthDays * 3 },
        { label: '6 meses', value: monthDays * 6 },
        { label: '1 año', value: monthDays * 12 },
        { label: '2 años', value: monthDays * 24 },
    ];
    validValue = rightTimeRange(+value, options);

    return <OptionSelect options={options} onChange={onChange} value={validValue} label={label} />;
};
