import React, { FunctionComponent, Suspense, useEffect, useState } from 'react';
import { ErrorBoundary } from 'react-error-boundary';
import { useDispatch } from 'react-redux';
import { ErrorDisplay, Loader } from '~/ui/components';
import { useAPIResource } from '~/ui/utils/api';
import logger from '~/ui/utils/logger';
import { disableMarker } from '~F/price-track/redux/PriceTrackNotifications.saga';
import {
    IPriceTrackNotification,
    NotificationStatus,
    priceTrackAPI,
    priceTrackNotificationsAPI,
} from '../../api';
import { PriceTrackNotificationItem } from '../../components';
import styles from './PriceTrackNotifications.module.scss';
import { WithSuspense } from '~/ui/utils/api/api.suspend';

const noNotificationsError = 'NO_NOTIFICATIONS_ERROR';
const noPriceTracksError = 'NO_PRICE_TRACKS';

const PriceTrackNotifications: FunctionComponent<{ data: IPriceTrackNotification[] }> = ({
    data,
}) => {
    const [notifications, setNotifications] = useState(data);
    const [priceTracks] = useAPIResource(priceTrackAPI);
    const dispatch = useDispatch();
    const isPinfoOn = JSON.parse(import.meta.env.VITE_FT_PI as string);
    const deleteNoti = (id: string) => {
        setNotifications(notifications.filter((noti) => noti.priceTrackNotificationId !== id));
        priceTrackNotificationsAPI.methods.remove(id);
    };
    useEffect(() => {
        notifications.forEach((noti) => (noti.status = 0)); // notli mock flow
        if (isPinfoOn && notifications.some((n) => n.status !== NotificationStatus.Read)) {
            dispatch(disableMarker());
        }
    }, []);

    if (notifications.length === 0) {
        if (priceTracks.length > 0) {
            throw new Error(noNotificationsError);
        }
        throw new Error(noPriceTracksError);
    }

    notifications.sort((a, b) => {
        if (a.notifiedAt === null || b.notifiedAt === null) return 1;
        return a.notifiedAt > b.notifiedAt ? -1 : 1;
    });
    return (
        <div className={styles.container}>
            {notifications.map((n) => (
                <PriceTrackNotificationItem
                    notification={n}
                    deleteNoti={deleteNoti}
                    key={n.priceTrackNotificationId}
                />
            ))}
        </div>
    );
};

export default () => (
    <ErrorBoundary
        fallbackRender={({ error }) => {
            logger.error(error.message, error.stack);
            switch (error.message) {
                case noNotificationsError:
                    return <ErrorDisplay dialog="no_down_price" faceExpression="ashame" />;
                case noPriceTracksError:
                    return <ErrorDisplay dialog="no_alert" faceExpression="ashame" />;
            }
            return <ErrorDisplay dialog="unknown" />;
        }}
    >
        <Suspense fallback={<Loader />}>
            <WithSuspense api={priceTrackNotificationsAPI} component={PriceTrackNotifications} />
        </Suspense>
    </ErrorBoundary>
);
