import { useEffect, useLayoutEffect, useState } from 'react';
import { createSubject } from '.';
import { APIHandler, APIResource } from './api.types';
import { createResource, ObservableAPIStore } from './api.util';

export function useAPIResource<Methods, TResource>(
    apiHandler: APIHandler<Methods, TResource>,
    { syncError = false } = {},
): [
    TResource,
    ObservableAPIStore.APIObservable<Methods, TResource>,
    { isLoading: boolean },
    // IAPIObservable<TResource>
] {
    const obAPI = ObservableAPIStore.get(apiHandler);
    const [data, setData] = useState<TResource>(() => obAPI.readSync());
    const [isLoading, setIsLoading] = useState(true);
    useEffect(() => {
        // const valueSub = obAPI.onUpdate$.pipe(
        //     startWith(undefined),
        //     combineLatestWith(obAPI.onError$.pipe(startWith(undefined))),
        //     take(1),
        //     concatWith(obAPI.onUpdate$.pipe(map((x) => [x, undefined] as [TResource, unknown]))),
        // )
        // .subscribe({
        //     next([data, error]) {
        //         if (data !== undefined) {
        //             setData(data);
        //         } else if (error !== undefined) {
        //             setData(() => {
        //                 throw error;
        //             });
        //         }
        //     },
        // });
        const valueSub = obAPI.onUpdate$
            // .pipe(map((x) => [x, undefined] as [TResource, unknown]))
            .subscribe({
                next(data) {
                    setData(data);
                },
            });
        const isLoadingSub = obAPI.isLoading$.subscribe({ next: setIsLoading });
        return () => {
            valueSub.unsubscribe();
            isLoadingSub.unsubscribe();
            // errorSub.unsubscribe();
        };
    }, []);

    return [
        obAPI.readSync(),
        obAPI,
        { isLoading },
        // subject as IAPIObservable<TResource>
    ];
}

export function useAPIResourceOld<Methods, TResource>(
    apiHandler: APIHandler<Methods, TResource>,
    { syncError = false } = {},
): [
    TResource,
    APIResource<Methods, TResource>,
    // IAPIObservable<TResource>
] {
    const apiResource = createResource(apiHandler);
    const [resource, setResource] = useState(apiResource.read());
    const [subject, setSubject] = useState(() => {
        let dataHandler = (data: TResource) => {
            setResource(data);
        };
        let errorHandler = !syncError
            ? undefined
            : (e: Error) => {
                  setResource(() => {
                      throw e;
                  });
              };
        return createSubject(setResource, errorHandler);
    });

    useLayoutEffect(() => {
        return apiResource.onUpdate(subject);
    }, [subject]);

    return [
        resource,
        apiResource,
        // subject as IAPIObservable<TResource>
    ];
}

export function useAPI<Methods, TResource>(
    apiHandler: APIHandler<Methods, TResource>,
): [TResource | null, boolean, ObservableAPIStore.APIObservable<Methods, TResource>] {
    const obAPI = ObservableAPIStore.get(apiHandler);

    const [data, setData] = useState<TResource | null>(() => {
        try {
            return obAPI.readSync();
        } catch (e) {
            return null;
        }
    });

    const [isLoading, setIsLoading] = useState(true);
    useEffect(() => {
        const valueDisp = obAPI.onUpdate$.subscribe({ next: setData });
        const isLoadingDisp = obAPI.isLoading$.subscribe({ next: setIsLoading });
        return () => {
            valueDisp.unsubscribe();
            isLoadingDisp.unsubscribe();
        };
    }, []);
    return [data, isLoading, obAPI];
}
