///<reference types="vite-plugin-svgr/client" />
import React from 'react';
import { ReactComponent as AshamedFace } from '../../assets/ashamed.inline.svg';
import { ReactComponent as ConfusedFace } from '../../assets/icons/confused.inline.svg';
import { ReactComponent as EmbarrassedFace } from '../../assets/icons/embarrassed.inline.svg';
import { ReactComponent as RelievedFace } from '../../assets/relieved.inline.svg';
import { FooterErrorPresenter } from './error-presenters/FooterErrorPresenter';
import { MainErrorPresenter } from './error-presenters/MainErrorPresenter';
import {
    DisplayMode,
    ErrorType,
    FaceExpression,
    IErrorDisplayBaseProps,
    IErrorDisplayPresenterProps,
} from './ErrorDisplay.types';
import { getErrorMessage, getErrorUserAction, getFacialComment } from './ErrorDisplay.utils';

function getFaceExpression(faceExpression: FaceExpression) {
    switch (faceExpression) {
        case 'relieved':
            return <RelievedFace />;
        case 'confused':
            return <ConfusedFace />;
        case 'embarrased':
            return <EmbarrassedFace />;
        case 'ashame':
        default:
            return <AshamedFace />;
    }
}

function getErrorIcon(errorType: ErrorType, faceExpression?: FaceExpression): JSX.Element {
    if (faceExpression) return getFaceExpression(faceExpression);

    switch (errorType) {
        case 'no_alert':
            return getFaceExpression('confused');
        case 'no_product':
            return getFaceExpression('embarrased');
        case 'no_down_price':
        case 'no_history_price':
        case 'no_pepper':
        case 'unknown':
            return getFaceExpression('ashame');
    }
}

export interface IErrorDisplayProps extends IErrorDisplayBaseProps {
    mode?: DisplayMode;
    faceExpression?: FaceExpression;
}

export const ErrorDisplay = (props: IErrorDisplayProps) => {
    const { mode = 'main' } = props;

    const innerProps: IErrorDisplayPresenterProps = {
        dialog: props.dialog,
        icon: getErrorIcon(props.dialog, props.faceExpression),
        message: getErrorMessage(props.dialog),
        userAction: getErrorUserAction(props.dialog),
        facialComment: getFacialComment(props.dialog),
    };

    switch (mode) {
        case 'footer':
            return <FooterErrorPresenter {...innerProps} />;
        case 'main':
        default:
            return <MainErrorPresenter {...innerProps} />;
    }
};

export default { ErrorDisplay };
